import Main from '@/views/Main.vue';

// Main
export const loginRouter = {
    path: '/login',
    name: 'login',
    meta: {
        title: 'Login - '
    },
    component: () => import('@/views/login.vue')
};

// MainotherRouter
export const otherRouter = {
    path: '/',
    name: 'otherRouter',
    redirect: '/home',
    component: Main,
    children: [
        {
            path: 'home',
            title: {i18n: 'home'},
            name: 'home_index',
            component: () => import('@/views/home/home.vue')
        },
        {
            path: 'ownspace',
            title: '',
            name: 'ownspace_index',
            component: () => import('@/views/own-space/own-space.vue')
        },
        {
            path: 'posts/edit/:id',
            title: '',
            name: 'edit_post',
            component: () => import('@/views/posts/post-edit.vue')
        }
    ]
};

// MainappRouter
export const appRouter = [
    {
        path: '/dashboard',
        icon: 'home',
        name: 'dashboard',
        title: '',
        component: Main,
        children: [
            {
                path: 'home',
                icon: 'home',
                title: '',
                name: 'home',
                component: () => import('@/views/home/home.vue')
            }
        ]
    },
    {
        path: '/posts',
        icon: 'document-text',
        name: 'posts',
        title: '',
        component: Main,
        children: [
            {
                path: 'post-list',
                title: '',
                name: 'artical-list',
                icon: 'document',
                component: () => import('@/views/posts/post-list.vue')
            },
            {
                path: 'post-add',
                title: '',
                name: 'post-add',
                icon: 'compose',
                component: () => import('@/views/posts/post-edit.vue')
            },
            {
                path: 'categorys',
                title: '',
                name: 'categorys',
                icon: 'navicon',
                component: () => import('@/views/posts/categorys.vue')
            },
            {
                path: 'post-trash',
                title: '',
                name: 'post-trash',
                icon: 'ios-trash',
                component: () => import('@/views/posts/post-trash.vue')
            },
            {
                path: 'post-tags',
                title: '',
                name: 'post-tags',
                icon: 'pricetags',
                component: () => import('@/views/posts/post-tags.vue')
            }
        ]
    },
    {
        path: '/extends',
        icon: 'hammer',
        name: 'extends',
        title: '',
        component: Main,
        children: [
            {
                path: 'navigations',
                title: '',
                name: 'navigations',
                icon: 'ios-navigate-outline',
                component: () => import('@/views/plugins/navigations.vue')
            },
            {
                path: 'links',
                title: '',
                name: 'links',
                icon: 'link',
                component: () => import('@/views/plugins/links.vue')
            },
            {
                path: 'comments',
                title: '',
                name: 'comments',
                icon: 'chatboxes',
                component: () => import('@/views/plugins/comments.vue')
            },
            {
                path: 'options',
                title: '',
                name: 'options',
                icon: 'ios-settings',
                component: () => import('@/views/plugins/options.vue')
            }
        ]
    },
    {
        path: '/settings',
        icon: 'settings',
        name: 'settings',
        title: '',
        component: Main,
        children: [
            {
                path: 'setting',
                title: '',
                name: 'setting',
                component: () => import('@/views/plugins/settings.vue')
            }
        ]
    }
];


// routers
export const routers = [
    loginRouter,
    otherRouter,
    ...appRouter
];



